/*===RESET===*/
html, body, div, span, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, a, img, strong, b, i, ol, ul, li, form, label, input, textarea, button, header, footer, main, section, nav, article, aside { margin: 0; padding: 0; outline: none; font-weight: inherit; font-style: inherit; font-size: inherit; font-family: inherit; color: inherit; line-height: inherit; vertical-align: baseline; box-sizing: border-box; }

header, footer, main, section, nav, article, aside { display: block; }

button { overflow: visible; cursor: pointer; }

img { vertical-align: bottom; border: none; }

b, strong { font-weight: 700; }

i { font-style: italic; }

html, body { font-weight: 400; font-style: normal; line-height: 1; }

