/*===HEADER===*/
.header { display: block; width: 100%; position: fixed; top: 0; left: 0; z-index: 8; transition: .21s; }

.header_fixed, .header.toggled { background: #FFF; box-shadow: 0 1px 11px rgba(0, 0, 0, 0.21); }

.header__wrap { height: 100px; max-width: none; }

.header__logo { display: inline-block; width: 100%; max-width: 121px; }

.header__logo img { width: 100%; height: auto; }

.header__nav { float: left; }

.header__nav-wrap { float: right; }

.header__toggle { display: none !important; }

.header__user { float: left; margin-left: 25px; }

@media (min-width: 1001px) { .header { transform: translate3d(0, 0, 0); } }

@media (max-width: 1000px) { .header__nav { float: none; padding: 20px 0; }
  .header__nav-wrap { float: none; visibility: hidden; opacity: 0; position: fixed; top: 100px; bottom: 0; left: 0; width: 100%; overflow: auto; background: #FFF; border-top: 1px solid #dddddd; transition: .5s; }
  .header__nav-wrap.toggled { visibility: visible; opacity: 1; }
  .header__toggle { display: block !important; position: absolute !important; right: 15px; top: 50%; margin-top: -12px; } }

/*Menu*/
.menu { list-style: none; padding-top: 8px; }

.menu:after { display: block; width: 100%; content: ""; clear: both; }

.menu__item { float: left; margin-left: 45px; }

.menu__item:first-child { margin-left: 0; }

.menu__item_has-children { position: relative; }

.menu__item_has-children .menu__arr { display: inline-block; }

.menu__item_has-children:hover .menu__sub-menu { visibility: visible; opacity: 1; }

.menu__a { font-size: 18px; font-weight: 500; text-decoration: none; transition: .5s; }

.menu__a:hover { color: #ff0000; }

.menu__arr { display: none; width: 12px; height: 8px; position: relative; bottom: 2px; margin-left: 7px; border: none; background: none; background-image: url("../../dist/images/sprite.svg"); background-repeat: no-repeat; background-position: -39px -8px; background-size: 50px auto; }

.menu__sub-menu { position: absolute; list-style: none; width: 6000%; padding: 24px 0 16px 3000%; top: 100%; left: -3000%; visibility: hidden; opacity: 0; transition: .3s; }

.header_fixed .menu__sub-menu { background: #ffffff; box-shadow: 0 3px 3px rgba(0, 0, 0, 0.11); }

.menu__sub-menu-item { float: left; margin-left: 25px; }

.menu__sub-menu-item:first-child { margin-left: 0; }

.menu__sub-menu-a { font-size: 14px; font-weight: 300; text-decoration: none; display: inline-block; transition: .5s; }

.menu__sub-menu-a:hover { color: #ff0000; }

@media (max-width: 1000px) { .menu { padding: 10px 25px; }
  .menu__item { float: none; text-align: center; margin: 25px 0; }
  .menu__item_has-children .menu__a { margin-left: 21px; }
  .menu__sub-menu { width: auto; left: 0; padding: 14px 0 0; box-shadow: none !important; position: relative; visibility: visible; opacity: 1; display: none; }
  .menu__sub-menu-item { float: none; margin: 0; padding: 9px 0; }
  .menu__item_has-children:hover .menu__sub-menu { display: block; } }

/*User*/
.user { height: 34px; position: relative; }

.user__thumb { display: inline-block; width: 34px; height: 34px; border-radius: 34px; vertical-align: middle; overflow: hidden; }

.user__name { font-size: 14px; display: inline-block; vertical-align: middle; margin-right: 21px; text-align: right; }

.user__link { text-decoration: none; transition: .5s; }

.user__link:hover { color: #ff0000; }

.user__link-logout { font-size: 13px; margin-top: 3px; opacity: .8; }

.user__bubble { width: 250px; top: 45px; right: 0; padding: 25px; }

/*===BLOCK===*/
/*video*/
.video { position: relative; cursor: pointer; padding-top: 20px; }

.video__thumb { position: relative; }

.video__thumb:before { display: block; content: ""; width: 100%; padding-bottom: 56.875%; }

.video__thumb img { position: absolute; left: 0; top: 0; z-index: 2; }

.video__thumb:after { display: block; content: ""; position: absolute; width: 100%; height: 100%; left: 0; top: 0; box-sizing: border-box; z-index: 3; border: 8px solid #895df7; opacity: 0; }

.video__play { position: absolute; left: 50%; top: 50%; z-index: 4; margin: -20px 0 0 -29px; visibility: hidden; opacity: 0; }

.video__chanal { width: 70px; height: 70px; position: relative; bottom: 15px; left: 13px; border: 2px solid #895df7; text-align: center; background: #FFF; z-index: 3; float: left; }

.video__tit { margin: 18px 0 0 108px; font-size: 14px; font-weight: 700; line-height: 1.35714; }

.video_focus .video__thumb { transform: scale(1.1); }

