  @keyframes next_to_prev { 50% { transform: scale(0.85) translate(150%);
      opacity: 0; }
    55% { transform: scale(0.85) translate(-150%);
      opacity: 0; }
    100% { transform: scale(0.85) translate(-108%);
      opacity: .5; } }
  @keyframes from_prev { 90% { transform: scale(0.85) translate(-150%);
      opacity: 0; }
    100% { transform: scale(0.85) translate(0);
      opacity: 0; } }
  @keyframes to_next { 50% { transform: scale(0.85) translate(150%);
      opacity: 0; }
    100% { transform: scale(0.85) translate(108%);
      opacity: .5; } }
  @keyframes from_next { 90% { transform: scale(0.85) translate(150%);
      opacity: 0; }
    100% { transform: scale(0.85) translate(0);
      opacity: 0; } }
  @keyframes to_prev { 55% { transform: scale(0.85) translate(-150%);
      opacity: 0; }
    100% { transform: scale(0.85) translate(-108%);
      opacity: .5; } } }

/*===SCROLLPANE===*/
.jspContainer { overflow: hidden; position: relative; }

.jspPane { position: absolute; }

.jspVerticalBar { position: absolute; top: 0; right: 0; width: 22px; height: 100%; }

.jspHorizontalBar { position: absolute; bottom: 0; left: 0; width: 100%; height: 16px; background: rd; }

.jspCap { display: none; }

.jspHorizontalBar .jspCap { float: left; }

.jspTrack { background: linear-gradient(to bottom, #fff5a3, #fffbda); position: relative; border-radius: 20px; }

.jspDrag { background: #ff0000; position: relative; top: 0; left: 0; cursor: pointer; border-radius: 20px; }

.jspHorizontalBar .jspTrack, .jspHorizontalBar .jspDrag { float: left; height: 100%; }

.jspArrow { background: #50506d; text-indent: -20000px; display: block; cursor: pointer; padding: 0; margin: 0; }

.jspArrow.jspDisabled { cursor: default; background: #80808d; }

.jspVerticalBar .jspArrow { height: 16px; }

.jspHorizontalBar .jspArrow { width: 16px; float: left; height: 100%; }

.jspVerticalBar .jspArrow:focus { outline: none; }

.jspCorner { background: #eeeef4; float: left; height: 100%; }

/*===ACCORD===*/
.accord { border-top: 1px solid #dddddd; background: #FFF; }

.accord__button { display: block; padding: 20px 80px 20px 20px; position: relative; width: 100%; text-align: left; border: none; background: none; border-bottom: 1px solid #dddddd; -webkit-appearance: none; }

.accord__button span { display: block; width: 40px; height: 40px; border-radius: 40px; position: absolute; right: 20px; top: 50%; margin-top: -20px; border: 1px solid #dddddd; }

.accord__button span:after { display: block; content: ""; position: absolute; width: 100%; height: 100%; left: 0; top: 0; }

.accord__content { display: none; border-bottom: 1px solid #dddddd; }

/*===TAB===*/
.tab__buttons { list-style: none; }

.tab__buttons:after { display: block; width: 100%; content: ""; clear: both; }

.tab__buttons li { float: left; }

.tab__button_active { color: #212121 !important; }

.tab__container { position: relative; transition: height .5s; }

.tab__item { position: absolute; left: 0; top: 0; width: 100%; visibility: hidden; opacity: 0; transition: .5s; }

.tab__item_active { visibility: visible; opacity: 1; }

/*===MORE===*/
.more { max-height: 97px; }

.more_apply { max-height: none; padding-bottom: 35px; position: relative; }

.more__inner { overflow: hidden; transition: .5s; }

.more__button { display: block; position: absolute; bottom: 11px; right: 0; }

/*===TEXT===*/
/*Title*/
.title { font-size: 26px; font-weight: 700; margin-bottom: 0.96154em; }

.title-h2 { font-size: 18px; font-weight: 500; margin-bottom: 1.38889em; }

/*Text*/
.txt { line-height: 1.375; }

.txt p { margin: 1.5625em 0; }

.txt p:first-child { margin-top: 0 !important; }

.txt p:last-child { margin-bottom: 0 !important; }

/*list*/
ul.list { list-style: none; }

ul.list li { line-height: 1.5; padding-left: 30px; margin: 1.875em 0; position: relative; }

ul.list li:before { display: block; content: ""; width: 9px; height: 9px; position: absolute; left: 3px; top: 6px; background: #ff0000; }

/*link*/
.link { text-decoration: none; border-bottom: 2px solid #00ff00; transition: .5s; }

.link:hover { border-bottom-color: transparent; }

/*text color*/
.c-red { color: #ff0000; }

.c-gray { color: #aaaaaa; }

/*text align*/
.ta-c { text-align: center; }

.ta-r { text-align: right; }

/*===BUTTON===*/
.button { display: inline-block; font-size: 14px; font-weight: 500; letter-spacing: 1px; padding: 16px 28px 14px; text-align: center; color: #FFF; border: none; background: #212121; text-transform: uppercase; text-decoration: none; -webkit-appearance: none; user-select: none; transition: .5s; }

.button_arr:after { display: inline-block; content: ""; width: 7px; height: 11px; background-image: url("../../dist/images/sprite.svg"); background-repeat: no-repeat; background-position: -43px -1px; background-size: 50px auto; margin-left: 5px; }

.button_w220 { width: 100%; max-width: 220px; }

.button_w-full { width: 100%; }

.button.lock { display: none; }

@media (min-width: 1200px) { .button:hover { opacity: .6; } }

@media (max-width: 1000px) { .button { width: 100%; max-width: none; } }

/*toggle*/
.toggle { display: inline-block; width: 28px; height: 21px; position: relative; background: none; border: none; }

.toggle span { height: 3px; background: #ff0000; position: absolute; left: 0; width: 100%; transition: .5s; }

.toggle span:first-child { top: 0; }

.toggle span:last-child { bottom: 0; }

.toggle span:nth-child(2), .toggle span:nth-child(3) { top: 9px; }

.toggle.toggled span:first-child, .toggle.toggled span:last-child { opacity: 0; }

.toggle.toggled span:nth-child(2) { transform: rotate(-45deg); }

.toggle.toggled span:nth-child(3) { transform: rotate(45deg); }

.on-toggled-hide.toggled { display: none !important; }

.on-toggled-show.toggled { display: block !important; }

/*button close*/
.btn-close { display: inline-block; width: 25px; height: 25px; position: relative; background: none; border: none; }

.btn-close:after, .btn-close:before { display: block; content: ""; width: 130%; height: 3px; background: #ff0000; position: absolute; left: -15%; top: 50%; margin-top: -1px; transition: .5s; }

.btn-close:after { transform: rotate(-45deg); }

.btn-close:before { transform: rotate(45deg); }

.btn-close:hover:after, .btn-close:hover:before { background: #000; }

.btn-close_v1 { width: 20px; height: 20px; background: #74C447; }

.btn-close_v1:after, .btn-close_v1:before { height: 2px; background: #FFF; }

/*button play*/
.btn-play { display: inline-block; width: 64px; height: 64px; border-radius: 64px; background: #ff0000; border: none; transition: .5s; }

.btn-play:before { display: block; content: ""; position: absolute; width: 0; height: 0; border: 7px solid transparent; border-left: 11px solid #fff; top: 50%; left: 50%; margin: -6px 0 0 -3px; }

.btn-play:hover { opacity: 0.7; }

/*Social*/
.social { list-style: none; }

.social:after { display: block; width: 100%; content: ""; clear: both; }

.social li { float: left; margin-right: 23px; }

.social li:last-child { margin: 0; }

.social__a { display: inline-block; width: 20px; height: 20px; transition: .5s; }

.social__a:hover { opacity: .6; }

/*===IMAGE===*/
.cover-img-css { position: relative; border: 1px solid #ff0000; overflow: hidden; }

.cover-img-css span { display: block; position: absolute; }

.cover-img-css img { display: inline-block; width: auto; height: auto; max-width: 100%; max-height: 100%; }

.cover-img { display: block; opacity: 0; transition: opacity .21s; }

.cover-img_w { width: 100%; height: auto; min-height: 100%; opacity: 1 !important; }

.cover-img_h { height: 100%; width: auto; min-width: 100%; opacity: 1 !important; }

.cover-img-wrap { overflow: hidden; }

.cover-img-wrap img { opacity: 0; transition: opacity .21s; }

.responsive-img, .middle__img, .bottom__img { display: inline-block; width: auto; height: auto; max-width: 100%; max-height: 100%; }

.middle__img { vertical-align: middle; }

.bottom__img { vertical-align: bottom; }

.full-width-img { display: block; width: 100%; height: auto; }

/*===POPUP===*/
.popup { position: fixed; top: 0; left: 0; width: 100%; height: 100%; overflow: auto; background: #000; background: rgba(0, 0, 0, 0.5); z-index: 9; text-align: center; font-size: 0; visibility: hidden; opacity: 0; transition: .5s; }

.popup_visible { visibility: visible; opacity: 1; }

.popup:before { display: inline-block; content: ""; height: 100%; vertical-align: middle; }

.popup__window { display: inline-block; visibility: hidden; position: absolute; vertical-align: middle; width: 100%; padding: 35px 0; font-size: 1rem; text-align: left; transform: scale(0); transition: .5s; }

.popup__window_visible { position: relative; visibility: visible; transform: scale(1); }

.popup__inner { background: #FFF; padding: 50px; }

.popup__inner:after { display: block; width: 100%; content: ""; clear: both; }

.popup__message { font-size: 18px; }

.popup__close { position: absolute !important; right: -29px; top: 4px; z-index: 3; }

/*popup-message*/
.popup-message { max-width: 560px; }

/*popup-media*/
.popup-media { max-width: 1024px; height: 95%; }

.popup-media .popup__inner { height: 100%; position: relative; }

.popup-media .popup__close { right: 10px; top: 50px; }

.popup-media__box { background: #000; position: relative; text-align: center; height: 100%; }

.popup-media__image { visibility: hidden; opacity: 0; transition: .5s; }

.popup-media__image_visible { visibility: visible; opacity: 1; }

.popup-media__video { background-position: cover; }

.popup-media__video, .popup-media__iframe { position: absolute; width: 100%; height: 100%; left: 0; top: 0; visibility: hidden; border: none; transition: .5s; }

.popup-media__video_visible, .popup-media__iframe_visible { visibility: visible; opacity: 1; }

.popup-media__bar { padding-top: 34px; font-size: 14px; display: none; position: absolute; bottom: 0; left: 0; width: 100%; height: 50px; padding: 10px 50px; }

.popup-media__bar-date { font-size: 14px; color: #666; }

.popup-media__bar-tit { font-size: 14px; font-weight: 700; }

.popup-media__arr { position: absolute; width: 12px; height: 20px; top: 50%; margin-top: -10px; z-index: 3; background-image: url("../../dist/images/sprite.svg"); background-repeat: no-repeat; background-size: 50px auto; display: none; }

.popup-media__arr_l { left: 53px; background-position: -24px -72px; }

.popup-media__arr_r { right: 53px; background-position: -38px -72px; }

.popup-media__play { display: block; width: 51px; height: 51px; border-radius: 51px; background: #FFF; position: absolute; z-index: 3; left: 50%; top: 50%; margin: -25px 0 0 -25px; box-shadow: 0 -4px 20px rgba(0, 0, 0, 0.21); opacity: .95; visibility: hidden; }

.popup-media__play:before { display: block; content: ""; position: absolute; width: 0; height: 0; border: 7px solid transparent; border-left: 11px solid #5aa75e; top: 50%; left: 50%; margin: -6px 0 0 -3px; }

@media (max-width: 1200px) { .popup-media { max-width: 904px; }
  .popup-media .popup__close { right: 32px; top: 80px; }
